import React, { useState, useRef } from 'react';
import {
  Button,
  Input,
  FormControl,
  FormLabel,
  // FormHelperText,
  Modal,
  ModalOverlay,
  ModalContent,
  ModalHeader,
  ModalFooter,
  ModalBody,
  ModalCloseButton,
  useDisclosure,
  useToast,
  Textarea,
  Select
  // Center,
  // HStack,
  // Image,
  // VStack,
  // Checkbox,
} from '@chakra-ui/react';
// import { useDropzone } from 'react-dropzone';
import { useBloodStorageContext } from '../context/bloodStorage_context';
import axios from 'axios';
import { bloodStorages_url } from '../utils/constants';


function CreateNewBloodStorageModal() {
  const {
    new_bloodStorage: {
      name,
      amount,
      from,
      type,
      date,
      detail,
      A,
      B,
      AB,
      O
    },
    updateNewBloodStorageDetails,
    createNewBloodStorage,
  } = useBloodStorageContext();


  const [loading, setLoading] = useState(false);

  const [bloodO, setBLoodO]= useState([]);
  const [bloodA, setBLoodA]= useState([]);
  const [bloodB, setBLoodB]= useState([]);
  const [bloodAB, setBLoodAB]= useState([]);
  const getdata= async()=>{     

    const response = await axios.get(bloodStorages_url+'/getBlood');
   let ABLood =response.data.ABLood;
   let BBLood= response.data.BBLood;
   let OBLood =response.data.BBLood;
   let ABBLood =response.data.ABBLood;
  setBLoodA(ABLood);
  setBLoodO(OBLood);
  setBLoodAB(ABBLood);
  setBLoodB(BBLood);
  // bloodDonates.sort((a, b) =>new Date(b.time).getTime()-new Date(a.time).getTime()).slice(0, 4).map(o => o);
 console.log(bloodA);
}
getdata();


  const { isOpen, onOpen, onClose } = useDisclosure();
  const initialRef = useRef();
  const toast = useToast();



  const handleSubmit = async () => {
    if (
      !name ||
      // !amount ||
      !from ||
      !type ||
      !date||
      !detail 
    ) {
      return toast({
        position: 'top',
        description: 'Provide all the details',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }
    if (
      type == 'Cho' &&
      bloodA < A 
    
    ) {
      return toast({
        position: 'top',
        description: 'Ch cn li '+ bloodA +'ml mu A',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }

    if (
      type == 'Cho' &&
      bloodB < B 
    
    ) {
      return toast({
        position: 'top',
        description: 'Ch cn li '+ bloodB +'ml mu B',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }

    if (
      type == 'Cho' &&
      bloodAB < AB 
    
    ) {
      return toast({
        position: 'top',
        description: 'Ch cn li '+ bloodAB +'ml mu AB',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }

    if (
      type == 'Cho' &&
      bloodO < O 
    
    ) {
      return toast({
        position: 'top',
        description: 'Ch cn li '+ bloodO +'ml mu O',
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }
    
    setLoading(true);
    console.log('uploading');
    const bloodStorage = {
        name,
        amount: 250/(A+B+O+AB),
        from,
        type,
        date,  
        A,
        B,
        AB,
        O,
        detail
    };
    const responseCreate = await createNewBloodStorage(bloodStorage);
    setLoading(false);
    if (responseCreate.success) {
      onClose();
      window.location.reload(false);
      return toast({
        position: 'top',
        description: 'bloodstorage created',
        status: 'success',
        duration: 5000,
        isClosable: true,
      });
    } else {
      return toast({
        position: 'top',
        description: responseCreate.message,
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }
  };

  return (
    <>
      <Button colorScheme='brown' onClick={onOpen}>
       Thm
      </Button>

      <Modal initialFocusRef={initialRef} isOpen={isOpen} onClose={onClose}>
        <ModalOverlay />
        <ModalContent>
          <ModalHeader>Thm</ModalHeader>
          <ModalCloseButton />
          <ModalBody pb={6}>
            <FormControl>
              <FormLabel>Tn</FormLabel>
              <Input
                ref={initialRef}
                placeholder='Tn'
                name='name'
                focusBorderColor='brown.500'
                value={name}
                onChange={updateNewBloodStorageDetails}
              />
            </FormControl>

            {/* <FormControl mt={4}>
              <FormLabel>S lng</FormLabel>
              <Input
                type='number'
                placeholder='S lng'
                name='amount'
                focusBorderColor='brown.500'
                value={amount}
                onChange={updateNewBloodStorageDetails}
              />
            </FormControl> */}

            <FormControl mt={4}>
              <FormLabel>T/n</FormLabel>
              <Input
               
                placeholder=''
                name='from'
                focusBorderColor='brown.500'
                value={from}
                onChange={updateNewBloodStorageDetails}
              />
            </FormControl>

            <FormControl mt={4}>
              <FormLabel>M t</FormLabel>
              <Textarea
                placeholder='M t'
                name='detail'
                focusBorderColor='brown.500'
                value={detail}
                onChange={updateNewBloodStorageDetails}
              />
            </FormControl>

            <FormControl mt={4}>
              <FormLabel>Ngy thc hin</FormLabel>
              <Input
                type='date'
                placeholder='Ngy thc hin'
                name='date'
                focusBorderColor='brown.500'
                value={date}
                onChange={updateNewBloodStorageDetails}
              />
            </FormControl>
            <FormControl mt={4}>
            <FormLabel>Chn mu</FormLabel>
            Mu O:
              <Input
             
                // placeholder='Mu O'
                name='O'
                focusBorderColor='brown.500'
                // value={O}
                onChange={updateNewBloodStorageDetails}
              />
              Mu A:
              <Input
             
            //  placeholder='Mu A'
             name='A'
             focusBorderColor='brown.500'
             // value={O}
             onChange={updateNewBloodStorageDetails}
           />
           Mu AB:
           <Input
             
            //  placeholder='Mu AB'
             name='AB'
             focusBorderColor='brown.500'
             // value={O}
             onChange={updateNewBloodStorageDetails}
           />
           Mu B:
           <Input
             
            //  placeholder='Mu B'
             name='B'
             focusBorderColor='brown.500'
             // value={O}
             onChange={updateNewBloodStorageDetails}
           />
            </FormControl>
            
            
            <FormControl mt={4}>
            <FormLabel>Loi</FormLabel>
              <Select
                name='type'
                focusBorderColor='brown.500'
                value={type}
                onChange={updateNewBloodStorageDetails}
              >
                <option value='Nhn'>Nhn</option>
                <option value='Cho'>Cho</option>
              
              </Select>
            </FormControl>

            
          </ModalBody>

          <ModalFooter>
            <Button mr={3} onClick={onClose}>
              Cancel
            </Button>
            <Button
              isLoading={loading}
              loadingText='Creating BloodStorage'
              colorScheme='brown'
              onClick={handleSubmit}
            >
              Save
            </Button>
          </ModalFooter>
        </ModalContent>
      </Modal>
    </>
  );
}

export default CreateNewBloodStorageModal;
