import React, { useState } from 'react';
// import { BiChevronDown } from 'react-icons/bi';
import { useBloodDonateContext } from '../context/bloodDonate_context';
import { Link } from 'react-router-dom';
import moment from 'moment';
import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
 
  Button,
  // Menu,
  // MenuButton,
  // MenuList,
  // MenuItem,
  SimpleGrid,

  HStack,
  Spinner,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  useToast,
} from '@chakra-ui/react';
import { BiChevronDown } from 'react-icons/bi';
// import UpdateBloodDonateModal from './UpdateBloodDonateModal';
// import UpdateStatusBloodDonateModal from './UpdateStatusBloodDonateModal'
import { useDonateContext } from '../context/donate_context';
// import { formatPrice } from '../utils/helpers';
function DonateTable({ bloodDonates }) {
  const toast = useToast();
  const { fetchBloodDonates, deleteBloodDonate } = useBloodDonateContext();
  const [loading, setLoading] = useState(false);

  // const {
  //   donates,
   
  // } = useDonateContext();
  



  const handleDelete = async (id) => {
    setLoading(true);
    const response = await deleteBloodDonate(id);
    setLoading(false);
    if (response.success) {
      toast({
        position: 'top',
        description: response.message,
        status: 'success',
        duration: 5000,
        isClosable: true,
      });
      return await fetchBloodDonates();
    } else {
      return toast({
        position: 'top',
        description: response.message,
        status: 'error',
        duration: 5000,
        isClosable: true,
      });
    }
  };

  return (
    <SimpleGrid bg='white' p={5} shadow='lg' borderRadius='lg' overflowX='auto'>
      {loading ? (
        <HStack my={8} alignItems='center' justifyContent='center'>
          <Spinner size='lg' color='brown.500' />
        </HStack>
      ) : (
        <Table variant='simple'>
          <Thead>
            <Tr>
            <Th>STT</Th>
              <Th>Tn bui hin</Th>
              <Th>Thi gian</Th>
              <Th>a im</Th>
              <Th>Trng thi</Th>
              <Th>Hnh ng</Th>
            </Tr>
          </Thead>
          <Tbody>
            {bloodDonates.map((bloodDonate, index) => {
              const {  name, time,timeF, address,status,id } =
                bloodDonate;
                if(status==='Cha thc hin'){
              return (
                <Tr key={index}>
                   <Td>{index+1}</Td>
                <Td>{name}</Td>
                <Td>{time.substr(10,5)+'-'+timeF.substr(10,5)}
                  <br/>{moment(timeF.substring(0,10)).format('DD/MM/YYYY')}
                  </Td>
                  <Td>{address}</Td>
                  <Td>{status}</Td>

                  <Td>
                  <Menu>
                          <MenuButton as={Button} rightIcon={<BiChevronDown />}>
                            Hnh ng
                          </MenuButton>
                          <MenuList>
                            <Link to={`/bookings/${id}`}>
                              <MenuItem>Xem danh sch hn</MenuItem>
                            </Link>
                            <Link to={`/donates/${id}`}>
                              <MenuItem>Chn</MenuItem>
                            </Link>
                          
                           
                          </MenuList>
                        </Menu>
                  {/* <Button ><Link to={`/bookings/${id}`}>Xem danh sch hn</Link></Button><br/><br/>
                  <Button ><Link to={`/donates/${id}`}>Chn</Link></Button> */}
                    {/* <Menu>
                      <MenuButton as={Button} rightIcon={<BiChevronDown />}>
                        Hnh ng
                      </MenuButton>
                      <MenuList>
                        <Link to={`/bloodDonate/${id}`}>
                          <MenuItem>Xem</MenuItem>
                        </Link>
                       
                        
                      </MenuList> */}
                    {/* </Menu> */}
                  </Td>
                </Tr>
              );}
            })}
          </Tbody>
        </Table>
      )}
    </SimpleGrid>
  );
}

export default DonateTable;
